<template>
    <div class="header">
        <!-- 这是header -->
        <div class="header-l">
            <!-- 侧边菜单收起图标 -->
            <el-icon @click="toggleMenu">
                <Expand v-if="$store.state.base.menuCollapse"/>
                <Fold v-else/>
                <!-- <componemt v-if="$store.state.base.menuCollapse?'Expand':'Fold'"/> -->
            </el-icon>
            <!-- 面包屑 -->
            <el-breadcrumb separator="/">
                <!-- 这里 默认首页写成展示 UserList 页 -->
                <el-breadcrumb-item v-for="item in breadList" :key="item.path">
                    <!-- <router-link :to="item.path">{{item.meta.title }}</router-link> -->
                    <router-link to="/home">{{item.meta.title }}</router-link>
                </el-breadcrumb-item>
                <!-- <el-breadcrumb-item :to="{ path: '/UserList' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item v-if="$route.path!=='/UserList'">
                    {{$route.name}}
                </el-breadcrumb-item> -->
            </el-breadcrumb>
        </div>
        <div class="header-r">
            <!-- 头像部分，下拉 -->
            <el-dropdown>
                <div class="user">
                    <!-- <span>用户昵称</span> -->
                    <span style="font-size: 20px; font-weight: 900; color:red">{{ $store.state.user.userInfo.nickName}}</span>
                    <!-- <el-avatar :size="50" :src="circleUrl" /> -->
                    <!-- <el-avatar :size="50" src="https://p.qqan.com/up/2020-7/15946191866421308.jpg" /> -->
                    <el-avatar :size="50" :src="$store.state.user.userInfo.avatar||'https://p.qqan.com/up/2020-7/15946191866421308.jpg'" />
                </div>
                <!-- <span class="el-dropdown-link">
                Dropdown List<el-icon class="el-icon--right"><arrow-down /></el-icon>
                </span> -->
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item>个人中心</el-dropdown-item>
                        <el-dropdown-item @click="exitLogin">退出登陆</el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
    </div>
</template>

<script>
export default{
    data(){
        return{
            breadList: [] // 路由集合
        }
    },
    created(){
        this.getBreadcrumb()
    },
    watch:{
        // watch中监听 点击菜单时，路由地址变化，也让面包屑breadList【】值监听变化赋值
        $route(){
            this.getBreadcrumb();
        }
    },
    methods:{
        // 退出登陆事件
        exitLogin(){
            // 方式1 ，退出登陆，我们可以清除token 然后刷新路由 this.$router.go(0)，因为清除token后 路由守卫里 我们写了判断是否有token 没有token我们在路由守卫中 写了跳转到登陆页
            // 方式2，我们清除token 不刷新当前路由 直接跳转到登陆页路由 this.$router.replace('/login') 或者this.$router.push('/login')
            localStorage.removeItem('token')
            localStorage.removeItem('userInfo')
            localStorage.removeItem('role')
            // 
            localStorage.removeItem('menus')
            localStorage.removeItem('authRoutes')

            this.$router.go(0)
        },
        // isHome(route) {
        //     return route.name === "home";
        // },
        getBreadcrumb() {
            // console.log(this.$route);
            let matched = this.$route.matched;
            //如果不是首页
            // if (!this.isHome(matched[0])) {
            //     matched = [{ path: "/home", meta: { title: "首页" } }].concat(matched);
            // }
            this.breadList = matched;
            // console.log(this.breadList);
        },
        // 侧边菜单收起事件方法
        toggleMenu(){
            // 方式一
            this.$store.commit('base/TOGGLE_menuCollapse')
        }
    }
}
</script>
<style lang="less" scoped>
    .header{
        height: 100%;
        display: flex;
        justify-content: space-between;
        &-l{
            display: flex;
            align-items: center;
            .el-icon{
                margin-right: 10px;
                cursor: pointer;
            }
        }
        &-r{
            cursor: pointer;
            .el-dropdown{
                height: 100%;
            }
            .user{
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
</style>