// vuex中引入路由 ，使用路由的方法 router.addRoute()
import router from '@/router'
// 定义一个处理后端路由数据 数据库不能懒加载的组件名
const loadView = (name) => {
    return () => import('@v/' + name)
  }

// 用来登陆时，存储用户信息的
