    // 命名空间，防止各模块之间命名相同时有冲突
    namespaced: true,
    state:{
        // 重点 每次刷新页面时，都会调用localStorage.getItem 去获取登陆时本地存储的用户信息，这样用户登陆信息就每次刷新页面又把用户信息存储到vuex中，就不会丢失了
        // 为什么要使用vuex缓存，因为vuex是浏览器运行缓存，项目获取读取数据速度快

        token:localStorage.getItem('token')||"",
        // 本地想要获取的信息 不是字符串时 进行json.parse之前 一定要判断有没有值，否则直接jison.parse是会报错的
        userInfo:localStorage.getItem('userInfo')?JSON.parse(localStorage.getItem('userInfo')):{},
        role:localStorage.getItem('role')||"",

        // /动态权限 侧边菜单数据 角色鉴权，后端接口的数据
        menus: localStorage.getItem('menus')? JSON.parse(localStorage.getItem('menus')):[],
        // 动态权限 用户可以访问的 路由的数据
        authRoutes: localStorage.getItem('authRoutes')?JSON.parse(localStorage.getItem('authRoutes')):[]

        //静态权限菜单 角色鉴权，根据角色不同 处理菜单 渲染不同 定义所有存在的组件，分别安排上角色
        // 这里 * 我们表示 都可以访问渲染的菜单 
        // 侧边的导航菜单的数据
        // menus: [
        //     {
        //         label: '开始页',
        //         icon: 'setting',
        //         path: '/home',
        //         roles: '*'
        //     },
        //     {
        //         label: '商品管理',
        //         icon: 'location',
        //         roles: ['admin', 'a', 'b'],
        //         children: [
        //             {
        //                 label: '商品列表',
        //                 path: '/ShopManage/shoplist',
        //                 roles: ['admin', 'a', 'b']
        //             },
        //             {
        //                 label: '商品分类列表',
        //                 path: '/ShopManage/shopclass',
        //                 roles: ['admin', 'a', 'b']
        //             },
        //             {
        //                 label: '商品排行',
        //                 path: '/ShopManage/shopsort',
        //                 roles: ['admin', 'a', 'b']
        //             }
        //         ]
        //     },
        //     {
        //         label: '商家列表',
        //         icon: 'setting',
        //         path: '/userlist',
        //         roles: ['admin', 'a', 'b']
        //     },
        //     {
        //         label: '大屏展示',
        //         icon: 'setting',
        //         roles: ['admin', 'a', 'b'],
        //         children: [
        //             {
        //                 label: '横纵滚动条',
        //                 path: '/monitoring/yield',
        //                 roles: ['admin', 'a', 'b']
        //             }
        //         ]
        //     },
        //     {
        //         label: '地图展示',
        //         icon: 'setting',
        //         // path: '/baiduMap',
        //         roles: ['admin', 'a', 'b'],
        //         children: [
        //             {
        //                 label: '地图一',
        //                 path: '/baiduMap/mapOne',
        //                 roles: ['admin', 'a', 'b']
        //             }
        //         ]
        //     },
        //     {
        //         label: '设置',
        //         icon: 'setting',
        //         path: '/set',
        //         roles: ['admin','a', 'b']
        //     },
        //     {
        //         label: 'admin无权限菜单',
        //         icon: 'setting',
        //         path: '/notPermisstionAdmin',
        //         roles: ['a', 'b']
        //     }
        // ]
    },
    //静态权限菜单  getters这里是我们用来 判断 不同角色 过滤不同的路由组件，进行构成新的对象，进行菜单侧边栏的 渲染   getters 相当于计算属性，他的值可以直接用来使用
    // getters: {
    //     authMenus (state) {
    //       const menus = JSON.parse(JSON.stringify(state.menus))
    //       const authMenus = []
    //       const role = localStorage.getItem('role')
    //       menus.forEach(menu => {
    //         if (menu.roles === '*' || menu.roles.includes(role)) {
    //           if (menu.children) {
