    //           }
    //           authMenus.push(menu)
    //         }
    //       })
    //       return authMenus
    //     }
    // },
    mutations: {
        // mutations是修改state的唯一方式
        // 我们要把用户信息存储在vuex的state状态数据中

        // 用户登录成功时，存储用户信息  定义的一个方法用来vue组件中 使用this.$store.commit('user/INIT_USER',参数2是用户登陆的接口数据res.data)触发传参
        INIT_USER(state,{token,userInfo,role}){
            state.token=token
            state.userInfo=userInfo
            state.role=role
        },

        // 动态角色权限
        // 获取用户权限导航菜单 存储到vuex中
        INIT_ROLE_MENUS (state, menus) {
            state.menus = menus
        },
        // 动态添加 权限路由
        INIT_USER_ROUTES (state, authRoutes) {
            // 将 authRoutes 动态添加 layout 主页路由子路由
            const _authRoutes = JSON.parse(JSON.stringify(authRoutes))
            _authRoutes.forEach(item => {
                // console.log(route.component);
                // 第一层菜单路由
                item.component = loadView(item.component)
                
                // 第二层菜单路由
                if(item.children&&item.children.length>0){
                    // 这是对象，一定要深拷贝
                    item=JSON.parse(JSON.stringify(item))
                    // 
                    item.children.forEach(item2=>{
                        // console.log(item2.component);
                        item2.component = loadView(item2.component)
                        // console.log(item2.component);
                    })
                }
                // 动态添加为 Layout路由子路由  router.addRoute('首页', route)
                router.addRoute('layout', item)
            })
            
            console.log(_authRoutes);
        }
    },
    actions: {
        
    }
