export default{
    namespaced: true,
    state:{
        // 导航栏是否折叠
        menuCollapse:false
    },
    mutations: {
        // 切换导航栏
        TOGGLE_menuCollapse(state){
            // 切换导航栏折叠状态
            state.menuCollapse = !state.menuCollapse;
        }
    },
    actions: {
        
    }
}