// 静态路由
const routes = [
  // 路由重定向
  { path: '/', redirect: '/home' },
  // 1 主页布局
  {
    // 给layout的页面 路径设置为/
    path:'/',
    name:'layout',
    // 主页的布局页，不能用路由懒加载，因为要开始就加载
    component:LayOut,
    // meta: { title: '首页' },
    meta: {
      title: '首页',
      roles: '*'
    },
    
    // 哪个组件路由下面的孩子，就会出现在该组件的 router-view视口里
    children:[
      // 1开始页
      {
        path:'/home',
        name:'home',
        component:()=>import('@/views/home'),
        // meta: { title: '开始页' },
        meta: {
          title: '开始页',
          roles: ['admin', 'a', 'b']
        },
      },
      // 功能
      {
        path:'/project',
        name:'project',
        // component:()=>import('@/views/project'),
        // // meta: { title: '功能' },
        meta: {
          title: '功能',
          roles: ['admin', 'a', 'b']
        },
        children:[
          {
            path:'/project/function',
            name:'function',
            component:()=>import('@/views/project/function/index.vue'),
            // meta: { title: '设备' },
            meta: {
              title: '功能列表',
              roles: ['admin', 'a', 'b']
            },
          },
        ]
      },
      // myTable
      {
        path:'/myTable',
        name:'myTable',
        component:()=>import('@/views/project/function/everyFunction/myTable.vue'),
        // meta: { title: '设置' },
        meta: {
          title: '表格',
          roles: ['admin', 'a', 'b']
        },
      },
      
      // 百度地图
      {
        path:'/baiduMap',
        name:'baiduMap',
        // component:()=>import('@/views/baiduMap'),
        // meta: { title: '百度地图' },
        meta: {
          title: '百度地图',
          roles: ['admin', 'a', 'b']
        },
        children:[
          {
            path:'/baiduMap/mapOne',
            name:'mapOne',
            component:()=>import('@/views/baiduMap/mapOne'),
            // meta: { title: '地图一' },
            meta: {
              title: '地图一',
              roles: ['admin', 'a', 'b']
            },
          },
        ]
      },
      
      
      // 4 设置
      {
        path:'/Set',
        name:'设置',
        component:()=>import('@/views/Set'),
        // meta: { title: '设置' },
        meta: {
          title: '设置',
          roles: ['admin', 'a', 'b']
        },
      },

      // admin无权限菜单, 关于路由配置在哪里，要看准备把这个页面显示在那个chiidren孩子设置的视口中
      {
        path:'/notPermisstionAdmin',
        name:'admin无权限菜单',
        component:()=>import('@/views/notPermisstionAdmin'),
        // meta: { title: 'admin无权限菜单' },
        meta: {
          title: 'admin无权限菜单',
          roles: ['a', 'b']
        },
      },

    ],
  },

  // 以下两个页面 都是全屏显示，不能设置在 layout主页的孩子视口中，只能拿到外面配置路由
  // 2 登陆页
  {
    path:'/login',
    name:'登陆页',
    // 登陆页，可以使用懒加载
    component:()=>import('@/views/LoginPage'),
    meta: {
      roles: ['admin', 'a', 'b']
    },
  },
  // 404页面
  {
    path: '/:pathMatch(.*)*', 
    name: '404页面', 
    component: ()=>import('@/views/NotFount'),
    meta: {
      roles: ['admin', 'a', 'b']
    },

  },
  // 防止地址栏输入路由时 没有权限时访问时，都跳转到这个页面
  {
    path: '/notPermisstionToPage', 
    name: '没有权限时访问时 都跳到这个页面', 
    component: ()=>import('@/views/notPermisstionToPage'),
    meta: {
      roles: ['admin', 'a', 'b']
    },
  },
  
  // {
  //   path: "/about",
  //   name: "about",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  // },
];

const router = createRouter({
  // history: createWebHashHistory(),
  // 地址栏路由模式，去掉/#/
  history: createWebHistory(),
  routes,
});

